.head-container{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.head-image{
    padding-top: 60px;
    width: auto;
    transition: max-width 1s, min-width 1s;
}

.head-text{
    font-family: "Hachi Maru Pop", cursive;
    margin: 5px 0 15px 0;
    transition: font-size 1s;
}

.head-image-expanded{
    min-width: 250px;
    max-width: 250px;
}
.head-image-contracted{
    min-width: 60px;
    max-width: 40px;
}

.head-text-expanded{
    font-size: 36px;
}
.head-text-contracted{
    font-size: 12px;
}